html {
  --color-hover: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  margin-right: 20px;
  padding: 23px 0;
  text-decoration: none;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list .nav_item:last-child .nav__link {
  margin-right: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list .is-active {
  color: var(--color-hover);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-hover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link[data-qa="hover"]:hover {
  color: var(--color-hover);
}

